html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  color: inherit;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  background: none;
  border: none;
  padding: 0;
}

:root {
  --light: #f8efec;
  --dark: #343539;
  --sable: #cda595;
  --green: #23a4a4;
  --dark-transparent: #3c322d29;
  --space-xxs: .5rem;
  --space-xs: 1rem;
  --space-s: 1.5rem;
  --space-m: 2rem;
  --space-l: 3rem;
  --space-xl: 4.5rem;
  --space-2xl: 6.5rem;
  --space-3xl: max(4rem, min(calc(2.57rem + 7.14vw), 9rem));
  --fs-sm: .75rem;
  --fs-base: .9375rem;
  --fs-md: max(.94rem, min(calc(.9rem + .18vw), 1.06rem));
  --fs-lg: max(1.25rem, min(calc(1.18rem + .36vw), 1.5rem));
  --fs-highlight: max(1.75rem, min(calc(1.32rem + 2.14vw), 3.25rem));
  --fs-xl: max(1.38rem, min(calc(1.27rem + .54vw), 1.75rem));
  --fs-2xl: max(1.75rem, min(calc(1.39rem + 1.79vw), 3rem));
  --fs-3xl: max(3.25rem, min(calc(2.32rem + 3.98vw), 6.5rem));
  --fs-4xl: max(3.25rem, min(calc(1.9rem + 6.76vw), 9rem));
  --txt-medium: 500;
  --txt-semibold: 600;
  --txt-bold: 700;
  --txt-black: 900;
  --font-hobeaux: hobeaux, sans-serif;
  --font-poppins: poppins, sans-serif;
  --size-xxs: 240px;
  --size-xs: 360px;
  --size-sm: 480px;
  --size-md: 768px;
  --size-lg: 1024px;
  --size-xl: 1440px;
  --size-xxl: 1920px;
  --ease: cubic-bezier(.5, 0, .1, 1);
  --rounded: max(1rem, min(calc(.86rem + .71vw), 1.5rem));
  --rounded-full: 9999px;
  --shadow: 0 20px 40px var(--dark-transparent);
}

@media (prefers-reduced-motion: reduce) {
  *, :before, :after {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

::selection {
  background-color: #23a4a4;
  background-color: var(--green);
  color: #f8efec;
  color: var(--light);
}

body {
  font-family: poppins, sans-serif;
  font-family: var(--font-poppins);
  font-weight: 500;
  font-weight: var(--txt-medium);
  background-color: #f8efec;
  background-color: var(--light);
  color: #343539;
  color: var(--dark);
  font-size: .9375rem;
  font-style: normal;
  font-size: var(--fs-base);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.6;
}

a {
